.pg-header, .pg-footer {
  position: relative;
  width: 100%;
  margin: 0;
  padding: 3px 0;
  color: #fff;
  text-align: center;
  background-color: #101010; }

html,
body,
div,
p,
span,
button,
select,
ul,
ol,
h1,
h2,
h3,
h4 {
  margin: 0;
  padding: 0; }

.pg {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  min-width: 992px;
  min-height: 100vh; }
  .pg-content {
    flex-grow: 1;
    width: 100%;
    padding: 32px 0;
    text-align: center;
    background: rgba(255, 255, 0, 0.2); }
  .pg-footer > *:first-child {
    margin-right: 8px; }

.game {
  display: flex;
  justify-content: space-around;
  width: 992px;
  height: 640px;
  margin: auto;
  border: 1px solid #000;
  background: rgba(0, 255, 0, 0.1); }

.col-lft {
  height: 100%;
  background: rgba(50, 90, 240, 0.2); }

.level {
  width: 224px;
  height: 32px;
  margin-bottom: 32px;
  border: 1px solid #000;
  background: #fff; }

.char-info {
  width: 224px;
  height: 512px;
  border: 1px solid #000;
  background: #fff; }

.col-mid {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  background: rgba(50, 90, 240, 0.2); }

.title {
  width: 480px;
  height: 64px;
  border: 1px solid #000;
  background: #fff; }

.stage {
  width: 480px;
  height: 480px;
  margin-bottom: 32px;
  border: 1px solid #000;
  background: #fff; }

.items {
  width: 256px;
  height: 32px;
  border: 1px solid #000;
  background: #fff; }

.col-rgt {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  background: rgba(50, 90, 240, 0.2); }

.enemies-remaining {
  height: 32px;
  border: 1px solid #000;
  background: #fff; }

.enemy-stats {
  width: 224px;
  height: 160px;
  border: 1px solid #000;
  background: #fff; }

.activity-log {
  width: 224px;
  height: 288px;
  border: 1px solid #000;
  background: #fff; }

.enemy-stats {
  width: 224px;
  height: 160px;
  border: 1px solid #000;
  background: #fff; }

.tips {
  width: 224px;
  height: 64px;
  border: 1px solid #000;
  background: #fff; }
